import { Weather } from '../../api';

const SET_DATA = 'SET_DATA';
const SET_ERROR = 'SET_ERROR';
const SET_FORECAST_WEATHER = 'SET_FORECAST_WEATHER';

let initialState = {
  data: null,
  forecastWeather: '',
  error: null,
};

export const weather = (state = initialState, action) => {
  switch (action.type) {
    case SET_DATA:
      return {
        ...state,
        data: action.payload,
      };

    case SET_ERROR:
      return {
        ...state,
        error: action.payload,
      };
    case SET_FORECAST_WEATHER:
      return {
        ...state,
        forecastWeather: action.payload,
      };
    default:
      return state;
  }
};

export const setData = (data) => ({
  type: SET_DATA,
  payload: data,
});
export const setError = (error) => ({
  type: SET_ERROR,
  payload: error,
});
export const setForecastWeather = (forecastWeather) => ({
  type: SET_FORECAST_WEATHER,
  payload: forecastWeather,
});

export const getLocationUser = (lat, lon) => async (dispatch) => {
  try {
    const response = await Weather.locationUserWeather(lat, lon);
    const forecastWeather = await Weather.getForecastWeather(lat, lon);
    response && dispatch(setData(response.data));
    dispatch(setForecastWeather(forecastWeather.data));
  } catch (error) {
    console.log(error);
  }
};

export const searchCity = (city) => async (dispatch) => {
  try {
    const response = await Weather.getCity(city);
    const { lon, lat } = response.data.coord;
    console.log('lon' + lon, 'lat' + lat);
    const forecastWeather = await Weather.getForecastWeather(lat, lon);
    dispatch(setData(response.data));
    dispatch(setForecastWeather(forecastWeather.data));
    dispatch(setError(null));
  } catch (err) {
    dispatch(setError(err.response.data));
  }
};
