import React from 'react';
import { CardCurrentWeather } from '../Common/CardCurrentWeather';
import CloudOutlinedIcon from '@material-ui/icons/CloudOutlined';
import { Typography, makeStyles } from '@material-ui/core';

const useStyles = makeStyles((theme) => ({
  root: {
    display: 'flex',
    flexDirection: 'column',
    justifyContent: 'center',
    alignItems: 'center',
  },
  text: {
    color: 'rgba(4, 30, 45, 0.8)',
  },
  icon: {
    fontSize: '4em',
    color: 'rgba(0, 138, 255, 0.7)',
  },
}));

export const MainBlock = ({ data }) => {
  const style = useStyles();

  return (
    <div className={style.root}>
      <CloudOutlinedIcon className={style.icon} color='primary' />
      <Typography className={style.text} component='h1' variant='h4'>
        Weather Project
      </Typography>
      <CardCurrentWeather data={data} />
    </div>
  );
};
