import React from 'react';
import { BoxCard } from '../Common/BoxCard';
import { CardForecastWeatherDay } from '../Common/CardForecastWeatherDay';
import moment from 'moment';

export const DaysBlock = ({ daily }) => {
  return (
    <>
      <CardForecastWeatherDay>
        {daily.slice(1, 6).map((item) => {
          return (
            <BoxCard
              key={item.dt}
              description={item.weather[0].description}
              icon={item.weather[0].icon}
              celsius={item.temp.day.toFixed(0)}
              date={moment.unix(item.dt).format('dddd')}
            />
          );
        })}
      </CardForecastWeatherDay>
    </>
  );
};
