import moment from 'moment';
import React from 'react';
import { BoxCard } from '../Common/BoxCard';
import { CardForecastWeatherDay } from '../Common/CardForecastWeatherDay';

export const HourBlock = ({ hourly }) => {
  return (
    <>
      <CardForecastWeatherDay>
        {hourly.slice(1, 6).map((item) => {
          return (
            <BoxCard
              key={item.dt}
              description={item.weather[0].description}
              icon={item.weather[0].icon}
              celsius={item.temp.toFixed(0)}
              date={moment.unix(item.dt).format('HH:mm')}
            />
          );
        })}
      </CardForecastWeatherDay>
    </>
  );
};
