import React from 'react';
import { Card, CardContent, CardMedia, Typography, makeStyles } from '@material-ui/core';
import { Preloader } from '../Preloader';
import { Form } from '../../Form';
import { Clock } from '../Clock';

const useStyles = makeStyles((theme) => ({
  root: {
    width: '100%',
    color: 'rgba(255, 255, 255, 0.45)',
    background: ' #0093E9',
    backgroundImage: 'linear-gradient(66deg, #0093E9 1%, #80D0C7 88%)',
    marginTop: theme.spacing(4),
    padding: theme.spacing(2, 1),
    gridArea: 'card',
    gridColumn: '1/2',
  },

  cardContent: {
    display: 'flex',
    flexDirection: 'column',
    justifyContent: 'center',
    alignItems: 'center',
  },
  infoTitle: {
    fontWeight: 300,
    color: '#fff',
  },
  image: {
    height: '8rem',
    width: '7rem',
  },
  celsius: {
    color: '#fff',
    textAlign: 'center',
  },
  error: {
    marginTop: theme.spacing(1),
    color: '#ad393991',
  },
  gridContainer: {
    display: 'grid',
    gridTemplateColumns: '1fr 1fr',
    gridTemplateAreas: `'card  form'
                        'card  clock'`,
    alignItems: 'center',
    justifyItems: 'center',
    gridColumnGap: '5rem',
  },
  gridTemplateForm: {
    gridArea: 'form',
  },
  gridTemplateClock: {
    gridArea: 'clock',
  },
}));

export const CardCurrentWeather = ({ data }) => {
  const style = useStyles();
  if (!data) return <Preloader />;

  const celsius = data.main.temp.toFixed(0);

  return (
    <>
      <div className={style.gridContainer}>
        <Card className={style.root}>
          <CardContent className={style.cardContent}>
            <Typography component='h2' variant='h6' className={style.infoTitle}>
              {data.sys.country} {data.name}
            </Typography>
            <CardMedia
              className={style.image}
              image={`http://openweathermap.org/img/wn/${data.weather[0].icon}@2x.png`}
            />
            <Typography component='h2' variant='h6'>
              {data.weather[0].description}
            </Typography>
            <Typography className={style.celsius} component='p'>
              {celsius}° С
            </Typography>
          </CardContent>
        </Card>
        <div className={style.gridTemplateForm}>
          <Form />
        </div>
        <div className={style.gridTemplateClock}>
          <Clock />
        </div>
      </div>
    </>
  );
};
