import React from 'react';
import { Typography, makeStyles, Box } from '@material-ui/core';
const useStyles = makeStyles((theme) => ({
  box: {
    display: 'flex',
    minHeight: '100%',
    flexDirection: 'column',
    alignItems: 'center',
    justifyContent: 'space-between',
  },
  image: {
    height: '8rem',
    width: '8rem',
    position: 'relative',
    top: '1.3rem',
  },
  description: {
    margin: 'auto',
    lineHeight: '1.5rem',
  },
  time: {
    color: '#0009',
    fontWeight: 'bold',
    fontSize: '1.1rem',
  },
  celsius: {
    color: '#fff',
    textAlign: 'center',
    fontSize: '1.3rem',
  },
  iconCelsius: {
    fontSize: '1.5rem',
  },
}));

export const BoxCard = ({ icon, description, celsius, date }) => {
  const style = useStyles();

  return (
    <Box className={style.box}>
      <img
        className={style.image}
        src={`http://openweathermap.org/img/wn/${icon}@2x.png`}
        alt='icon'
      />
      <Typography component='h2' variant='h6' className={style.description}>
        {description}
      </Typography>
      <Typography className={style.time} component='p'>
        {date}
      </Typography>
      <Typography className={style.celsius} component='p'>
        {celsius}
        <span className={style.iconCelsius}>°</span>
      </Typography>
    </Box>
  );
};
