import React from 'react';
import { useForm } from 'react-hook-form';
import { searchCity } from '../../store/WeatherReducer';
import { useDispatch } from 'react-redux';
import s from './index.module.scss';
import cl from 'classname';

export const Form = () => {
  const { register, handleSubmit } = useForm();
  const dispatch = useDispatch();

  const onSubmit = (data) => {
    const { city } = data;
    const newCity = city.replaceAll(' ', '').toLowerCase();
    dispatch(searchCity(newCity));
  };

  return (
    <form className={s.form_search} noValidate onSubmit={handleSubmit(onSubmit)}>
      <input
        className={s.input_search}
        type='search'
        placeholder='Search...'
        {...register('city')}
      />
      <button type='submit' className={cl(s.button_search, 'fa', 'fa-search')}></button>
    </form>
  );
};
