import React, { useEffect } from 'react';
import { MainBlock } from '../../MainBlock';
import { HourBlock } from '../../HourBlock';
import { Preloader } from '../Preloader';
import { useDispatch, useSelector } from 'react-redux';
import { useLocation } from '../../../hooks/useLocation';
import { getLocationUser } from '../../../store/WeatherReducer';
import { DaysBlock } from '../../DaysBlock';
import { Alert } from '@material-ui/lab';
import { makeStyles } from '@material-ui/core';

const useStyles = makeStyles((theme) => ({
  alert: {
    width: '100%',
    display: 'flex',
    justifyContent: 'center',
    alignItems: 'center',
    position: 'absolute',
    top: '0rem',
    fontSize: '1.2rem',
  },
}));

export const ContentContainer = () => {
  const style = useStyles();
  const dispatch = useDispatch();
  const location = useLocation();
  const data = useSelector((state) => state.weather.data);
  const error = useSelector((state) => state.weather.error);
  const forecastWeather = useSelector((state) => state.weather.forecastWeather);
  const { daily, hourly } = forecastWeather;

  const {
    coordinates: { lat, lng },
  } = location;
  useEffect(() => {
    dispatch(getLocationUser(lat, lng));
  }, [dispatch, lat, lng]);

  if (!data || !forecastWeather) return <Preloader />;
  return (
    <>
      {error !== null ? (
        <Alert severity='error' className={style.alert}>
          {error.message}
        </Alert>
      ) : (
        ''
      )}
      <MainBlock data={data} />
      <HourBlock hourly={hourly} />
      <DaysBlock daily={daily} />
    </>
  );
};
