import * as axios from 'axios';

const urlAPI = 'https://api.openweathermap.org/data/2.5/';
const apiKey = '&appid=ce335fa02e62e132e4e1fc701c37eb7a';
const standardParameter = '&units=metric&lang=ru';
const exclude = '&exclude=current,minutely,alerts';

const instance = axios.create({
  baseURL: `${urlAPI}`,
});

export const Weather = {
  locationUserWeather(latitude, longitude) {
    if (latitude && longitude)
      return instance.get(`weather?lat=${latitude}&lon=${longitude}${standardParameter}${apiKey}`);
  },

  getCity(city) {
    return instance.get(`weather?q=${city}${standardParameter}${apiKey}`);
  },
  getForecastWeather(lat, lon) {
    if (lat && lon)
      return instance.get(`onecall?lat=${lat}&lon=${lon}${standardParameter}${exclude}${apiKey}`);
  },
};
