import React from 'react';
import s from './index.module.scss';

export const Preloader = () => {
  return (
    <div className={s.loader}>
      <div style={{ '--i': 1 }}></div>
      <div style={{ '--i': 2 }}></div>
      <div style={{ '--i': 3 }}></div>
      <div style={{ '--i': 4 }}></div>
    </div>
  );
};
